import React, { useState } from "react";
import axios from "axios";
import Joi from "joi-browser";
import InputGroup from "./common/inputGroup";

// const murl = "http://192.168.0.104:8000";
const murl = "";
const usersApiEndpoint = murl + "/api/user/";

const schema = {
  profile: Joi.string().required().label("Profile"),
  username: Joi.string().required().label("Username"),
  first_name: Joi.string().required().label("First name"),
  last_name: Joi.string().required().label("Last name"),
  email: Joi.string().email().max(256).required(),
  password: Joi.string().min(5).required().label("Password"),
  confirm_password: Joi.any()
    .equal(Joi.ref("password"))
    .required()
    .label("Confirm password")
    .options({ language: { any: { allowOnly: "must match password" } } }),
  profile_picture: Joi.optional(),
  address_line1: Joi.string().required().label("Address line"),
  address_city: Joi.string().required().label("City"),
  address_state: Joi.string().required().label("State"),
  address_pincode: Joi.string().required().label("Pin code"),
};

function Signup(props) {
  const [processing, setProcessing] = useState(false);
  const [formData, setFormData] = useState({
    profile: "",
    first_name: "",
    last_name: "",
    profile_picture: "",
    username: "",
    email: "",
    password: "",
    confirm_password: "",
    address_line1: "",
    address_city: "",
    address_state: "",
    address_pincode: "",
  });

  const resetForm = () => {
    setFormData({
      profile: "",
      first_name: "",
      last_name: "",
      profile_picture: "",
      username: "",
      email: "",
      password: "",
      confirm_password: "",
      address_line1: "",
      address_city: "",
      address_state: "",
      address_pincode: "",
    });
    setErrors({});
  };

  const [errors, setErrors] = useState({});

  function doValidation(data) {
    console.log("data :>> ", data);
    const result = Joi.validate(data, schema, {
      abortEarly: false,
    });

    console.log("result :>> ", result);
    if (result.error) {
      const newErrorObj = {};
      result.error.details.map(
        (item) => (newErrorObj[item.path[0]] = item.message)
      );
      setErrors(newErrorObj);
      return false;
    } else {
      setErrors({});
      return true;
    }
  }

  const signup = (data) => {
    setProcessing(true);
    const formData = new FormData();
    Object.keys(data).forEach((key) => formData.append(key, data[key]));
    formData.append(
      "profile_picture",
      document.querySelector("#profilePicture").files[0]
    );
    axios
      .post(usersApiEndpoint, formData, {
        headers: { "Content-Type": "multipart/form-data" },
      })
      .then(function (response) {
        console.log("response :>> ", response);
        resetForm();
        props.history.replace(`/login`);
      })
      .catch(function (error) {
        console.log("error :>> ", error.response);
        console.log(error.response);
        alert(JSON.stringify(error.response.data));
      })
      .finally(() => {
        setProcessing(false);
      });
  };

  const doSignup = (e) => {
    e.preventDefault();
    if (doValidation(formData)) signup(formData);
  };

  return (
    <main className="container">
      <h1>Sign up form</h1>
      <form id="signUpForm">
        <div className="mb-3 oneline">
          <label htmlFor="profile" className="form-label">
            Account type
          </label>
          <select
            onChange={(e) =>
              setFormData({ ...formData, profile: e.currentTarget.value })
            }
            value={formData.profile}
            id="profile"
            className="form-select"
          >
            <option>Select account type</option>
            <option value="1">Patient</option>
            <option value="2">Doctor</option>
          </select>
        </div>
        <div
          className={"form-text " + (errors["profile"] ? "error" : "hidden")}
        >
          {errors["profile"]}
        </div>

        <InputGroup
          label="First Name"
          onChange={(e) =>
            setFormData({ ...formData, first_name: e.currentTarget.value })
          }
          value={formData.first_name}
          errorMsg={errors["first_name"]}
        />

        <InputGroup
          label="Last Name"
          onChange={(e) =>
            setFormData({ ...formData, last_name: e.currentTarget.value })
          }
          value={formData.last_name}
          errorMsg={errors["last_name"]}
        />

        <div className="mb-3 oneline">
          <label htmlFor="profilePicture" className="form-label">
            Profile Picture
          </label>
          <input
            id="profilePicture"
            className="form-control"
            type="file"
            accept="image/png, image/jpeg"
            onChange={(e) =>
              setFormData({
                ...formData,
                profile_picture: e.currentTarget.value,
              })
            }
            value={formData.profile_picture}
          />
          <div id="profilePictureHelp" className="form-text hidden"></div>
        </div>

        <InputGroup
          label="Username"
          onChange={(e) =>
            setFormData({ ...formData, username: e.currentTarget.value })
          }
          value={formData.username}
          errorMsg={errors["username"]}
        />

        <InputGroup
          label="Email"
          onChange={(e) =>
            setFormData({ ...formData, email: e.currentTarget.value })
          }
          value={formData.email}
          errorMsg={errors["email"]}
        />

        <InputGroup
          label="Password"
          onChange={(e) =>
            setFormData({ ...formData, password: e.currentTarget.value })
          }
          value={formData.password}
          errorMsg={errors["password"]}
          inputType="password"
        />

        <InputGroup
          label="Confirm password"
          onChange={(e) =>
            setFormData({
              ...formData,
              confirm_password: e.currentTarget.value,
            })
          }
          value={formData.confirm_password}
          errorMsg={errors["confirm_password"]}
          inputType="password"
        />

        <div className="mb-3">
          <label htmlFor="line1" className="form-label">
            Address
          </label>
          <input
            type="text"
            className="form-control"
            id="line1"
            placeholder="Line 1"
            onChange={(e) =>
              setFormData({ ...formData, address_line1: e.currentTarget.value })
            }
            value={formData.address_line1}
          />
          <div className="addressInputs">
            <input
              type="text"
              className="form-control"
              id="city"
              placeholder="City"
              onChange={(e) =>
                setFormData({
                  ...formData,
                  address_city: e.currentTarget.value,
                })
              }
              value={formData.address_city}
            />
            <input
              type="text"
              className="form-control"
              id="state"
              placeholder="State"
              onChange={(e) =>
                setFormData({
                  ...formData,
                  address_state: e.currentTarget.value,
                })
              }
              value={formData.address_state}
            />
            <input
              type="text"
              className="form-control"
              id="pincode"
              placeholder="Pincode"
              onChange={(e) =>
                setFormData({
                  ...formData,
                  address_pincode: e.currentTarget.value,
                })
              }
              value={formData.address_pincode}
            />
          </div>

          <div
            className={
              "form-text " + (errors["address_line1"] ? "error" : "hidden")
            }
          >
            {errors["address_line1"]}
          </div>
          <div
            className={
              "form-text " + (errors["address_city"] ? "error" : "hidden")
            }
          >
            {errors["address_city"]}
          </div>
          <div
            className={
              "form-text " + (errors["address_state"] ? "error" : "hidden")
            }
          >
            {errors["address_state"]}
          </div>
          <div
            className={
              "form-text " + (errors["address_pincode"] ? "error" : "hidden")
            }
          >
            {errors["address_pincode"]}
          </div>
        </div>

        <button
          id="submitForm"
          type="submit"
          className="btn btn-primary"
          onClick={doSignup}
          disabled={processing}
        >
          SignUp
        </button>
        {processing && <label>Logging in ...</label>}
      </form>
    </main>
  );
}

export default Signup;
