import React, { useEffect, useState } from "react";
import axios from "axios";
import Cookies from "universal-cookie";
import Joi from "joi-browser";
import InputGroup from "./common/inputGroup";

const murl = "";
const loginEndpoint = murl + "/auth/jwt/create";

const schema = {
  username: Joi.string().required().label("Username"),
  password: Joi.string().min(5).required().label("Password"),
};

const cookies = new Cookies();

function Login(props) {
  const [formData, setFormData] = useState({
    username: "",
    password: "",
  });

  const [processing, setProcessing] = useState(false);

  const resetForm = () => {
    setFormData({
      username: "",
      password: "",
    });
    setErrors({});
  };
  const [errors, setErrors] = useState({});

  function doValidation(data) {
    console.log("data :>> ", data);
    const result = Joi.validate(data, schema, {
      abortEarly: false,
    });

    console.log("result :>> ", result);
    if (result.error) {
      const newErrorObj = {};
      result.error.details.map(
        (item) => (newErrorObj[item.path[0]] = item.message)
      );
      setErrors(newErrorObj);
      return false;
    } else {
      setErrors({});
      return true;
    }
  }

  const login = (data) => {
    setProcessing(true);
    axios
      .post(loginEndpoint, data)
      .then(function (response) {
        console.log("response :>> ", response);
        resetForm();
        const accessToken = response.data.access;
        const refreshToken = response.data.refresh;
        cookies.set("accessToken", accessToken);
        cookies.set("refreshToken", refreshToken);
        props.history.replace("/");
      })
      .catch(function (error) {
        console.log("error :>> ", error.response);
        console.log(error.response);
        alert(error.response.data.detail);
      })
      .finally(() => {
        setProcessing(false);
      });
  };

  const doLogin = (e) => {
    e.preventDefault();
    if (doValidation(formData)) login(formData);
  };

  return (
    <main className="container">
      <h1>Login</h1>
      <form className="loginForm">
        <InputGroup
          label="Username"
          onChange={(e) =>
            setFormData({ ...formData, username: e.currentTarget.value })
          }
          value={formData.username}
          errorMsg={errors["username"]}
        />

        <InputGroup
          label="Password"
          onChange={(e) =>
            setFormData({ ...formData, password: e.currentTarget.value })
          }
          value={formData.password}
          errorMsg={errors["password"]}
          inputType="password"
        />

        <button
          id="submitForm"
          type="submit"
          className="btn btn-primary"
          onClick={doLogin}
          disabled={processing}
        >
          Login
        </button>
        {processing && <label>Logging in ...</label>}
      </form>
      <div className="mt-2">
        <button
          id="submitForm"
          type="submit"
          className="btn btn-primary"
          onClick={() => props.history.replace("/signup")}
        >
          Signup
        </button>
      </div>
    </main>
  );
}

export default Login;
