import React, { useEffect, useState } from "react";
import axios from "axios";
import Cookies from "universal-cookie";

const cookies = new Cookies();

const murl = "";
const url = murl + "/auth/users/me/";

function Home(props) {
  const [userInfo, setUserInfo] = useState({
    profile: "",
    first_name: "",
    last_name: "",
    profile_picture: "",
    username: "",
    email: "",
    password: "",
    confirm_password: "",
    address_line1: "",
    address_city: "",
    address_state: "",
    address_pincode: "",
  });

  const getUserInfo = async (accessToken) => {
    const result = await axios.get(url, {
      headers: { Authorization: `JWT ${accessToken}` },
    });
    console.log("result :>> ", result);
    setUserInfo(result.data);
  };

  useEffect(() => {
    const accessToken = cookies.get("accessToken");
    console.log("accessToken :>> ", accessToken);
    if (!accessToken) props.history.replace("/login");
    else getUserInfo(accessToken);
  }, []);

  const doLogout = (e) => {
    e.preventDefault();
    console.log("Removing tokens");
    cookies.remove("accessToken");
    cookies.remove("refreshToken");
    console.log("Tokens removed");
    props.history.replace("/login");
  };

  return (
    <main className="container">
      <h1>User Info</h1>
      <div className="contentDetails">
        <div>
          <span className="title">Account Type</span>
          <span className="content">{userInfo.profile}</span>
        </div>
        <div>
          <span className="title">Name</span>
          <span className="content">{`${userInfo.first_name} ${userInfo.last_name}`}</span>
        </div>
        <div>
          <span className="title">Profile Picture</span>
          <span className="content">
            <img src={userInfo.profile_picture} alt="profile picture" />
          </span>
        </div>
        <div>
          <span className="title">Username</span>
          <span className="content">{`${userInfo.username}`}</span>
        </div>
        <div>
          <span className="title">Email</span>
          <span className="content">{`${userInfo.email}`}</span>
        </div>
        <div>
          <span className="title">Address</span>
          <span className="content">{`${userInfo.address_line1} ${userInfo.address_city} ${userInfo.address_state} ${userInfo.address_pincode}`}</span>
        </div>
        <button className="btn btn-primary btn-sm" onClick={doLogout}>
          Logout
        </button>
      </div>
    </main>
  );
}

export default Home;
